body {
  position: relative;
  color: $black;
  font-family: $font-family-primary;
  font-size: 1.6rem;
  font-style: inherit;
  // font-weight: 400;
  line-height: 1.5;
  overflow-x: hidden;

  * {
    &:focus {
      outline: 3px solid blue;
    }

    &:focus:not(:focus-visible) {
      outline: none;
    }

    &:focus-visible {
      outline: 3px solid blue;
    }
  }
}

.container {
  max-width: $container-width;
  margin: 0 auto;
  padding-right: 1.8rem;
  padding-left: 1.8rem;

  @include respond(tablet) {
    padding-right: 2rem;
    padding-left: 2rem;
  }
}
